:root{
  --primary-color: #0fa;
  --secondary-color: #bc13fe;
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

body {
  user-select: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgb(4, 25, 47) 20%, rgb(8, 30, 55) 70.2%);
}

.board {
  width: 510px;
  height: 510px;
  display: flex;
  flex-direction: column;
  .row {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
  }
}

.selected{
  background-color: rgba(239, 116, 255, 0.116);
}

.square {
  width: 100%;
  height: 170px;
  display: grid;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  &:active {
    background-color: rgba(239, 116, 255, 0.116);
  }
  span {
    color: rgb(255, 255, 255);
    animation: pulsate 1.5s infinite alternate;  
    text-shadow: 0 0 .2rem #fff,
    0 0 2rem rgba(0, 255, 170, 0.596); 
    font-size: 92px;
  }
}
.points {
  font-family: Orbitron;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  span{
    font-size: 32px;
    color: rgb(255, 255, 255);
    text-shadow: 0 0 1px #fff, 0 0 2px #0fa, 0 0 12px #0fa;
    margin: 10px;
  }
  hr{
    width: 100%;
    color: rgb(255, 255, 255);
    box-shadow: 0 0 1px #fff, 0 0 2px #0fa, 0 0 12px #0fa;
  }
  h1 {
    font-size: 60px;
    color: rgb(255, 255, 255);
    text-shadow: 0 0 1px #fff, 0 0 2px #0fa, 0 0 12px #0fa;
    margin: 20px;
  }
  h2 {
    font-size: 32px;
    color: rgb(255, 255, 255);
    text-shadow: 0 0 2px #0fa, 0 0 10px #0fa;
    margin: 15px;
    margin-bottom: 50px;
  }
}

.line-top {
  height: 4px;
  width: 100%;
  bottom: -2px;
  left: 0;
}
.line-bottom {
  height: 4px;
  width: 100%;
  bottom: -2px;
  left: 0;
}
.line-vertical-left {
  height: calc(300% + 4px);
  width: 4px;
  left: 33%;
  top: -2px;
}
.line-vertical-right{
  height: calc(300% + 4px);
  width: 4px;
  left: 66%;
  bottom: -2px;
}
.line {
  position: absolute;
  content: "";
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0 4px #fff,
  0 0 8px #0fa,
  0 0 15px #0fa;
  border-radius: 99px;
}
@media (max-width: 768px) {
  .points {
    span{
      font-size: 28px;
    }
    h1 {
      font-size: 60px;
    }
    h2 {
      font-size: 26px;
    }
  }
  .square {
    height: 140px;
    span {
      font-size: 68px;
    }
  }
  .board {
    width: 420px;
    height: 420px;
  }
}
@media (max-width: 576px) {
  .points {
    span{
      font-size: 24px;
    }
    h1 {
      font-size: 52px;
    }
    h2 {
      font-size: 22px;
    }
  }
  .square {
    height: 100px;
    span {
      font-size: 52px;
    }
  }
  .board {
    width: 300px;
    height: 300px;
  }
}
@media (max-width: 320px) {
  .points {
    span{
      font-size: 18px;
    }
    h1 {
      font-size: 42px;
    }
    h2 {
      font-size: 18px;
    }
  }
  .square {
    height: 80px;
    span {
      font-size: 42px;
    }
  }
  .board {
    width: 240px;
    height: 240px;
  }
}

@keyframes pulsate {
  100% {
      text-shadow:
      0 0 2px #fff,
      0 0 8px #0fa,
      0 0 10px #0fa
  }
  0% {
    text-shadow:
    0 0 2px #fff,
    0 0 5px #0fa,
    0 0 20px #0fa
}
}